import { Injectable } from '@angular/core';
import { IPosition, Network } from '@shared/common';
import { Broker } from '../broker/broker.service';
import { Smartloc } from '../smartloc/smartloc.service';
import { Requestor } from '../requestor/requestor.service';
import { Googlemaps } from '../googlemaps/googlemaps.service';
import { Authentication } from '../authentication/authentication.service';
import { Config } from '../config/config.service';
import { Session } from '../session/session.service';
import { Cache } from '../cache/cache.service';

import { Location } from '../../interfaces/location/location.interface';
import { LocationType } from '../../interfaces/location-type/location-type.interface';
import { ResponseObject } from '../../interfaces/response-object/response-object.interface';
import { CACHE_KEYS } from '../../interfaces/cache/cache.interface';

import { Filters, SubQuery, IHealthscore, ISort, IKpiData } from '@shared/stencil';

import { Observable, Observer, from } from 'rxjs';
import { map } from 'rxjs/operators';
import { isNumber } from 'lodash-es';

@Injectable()
export class Locations {
  constructor(private geoloc: Smartloc, private broker: Broker, protected rq: Requestor, protected config: Config, protected session: Session, protected network: Network, protected cache: Cache, protected authentication: Authentication, protected googleMaps: Googlemaps) {}

  loadMarkers(position: IPosition, maxPoints = 5000) {
    position = position || this.geoloc.defaultPosition;
    let filters = [
      [
        {
          field: '_geoloc',
          operator: { _id: 'nearSphere' },
          value: [position.longitude, position.latitude],
          max: 40000
        }
      ]
    ];
    return this.broker.getAll('locations', ['_id', 'title', '_geoloc', 'address'], null, null, filters, null, 0, maxPoints).pipe(
      map(retVal => {
        return this.getMarkers(retVal.data);
      })
    );
  }

  getMarkers(locations: Array<Location>) {
    return this.broker.getMarkers(locations);
  }

  getMarkersData(collectionName: string, maxPoints: number = 5000, fields?: Array<string>, filters?: Filters, subQuery?: SubQuery) {
    return this.broker.getAll(collectionName, fields || ['_id', 'title', '_geoloc', 'address'], null, null, filters, null, 0, maxPoints, false, subQuery).pipe(
      map(retVal => {
        let markers = this.getMarkers(retVal.data);
        let legendColors = {
          available: 'energized',
          booked: 'info',
          validated: 'success',
          rejected: 'danger',
          tobevalidated: 'royal',
          archived: 'dark'
        };
        return { markers, legendColors };
      })
    );
  }

  getLegendColors() {
    let legendColors = {
      available: 'energized',
      booked: 'info',
      validated: 'success',
      rejected: 'danger',
      tobevalidated: 'royal',
      archived: 'dark'
    };
    return legendColors;
  }

  getLastVisitDate(locationIds: Array<string>, userId?: string): Observable<Array<{ _id: string; finishedDate: Date; count: number }>> {
    let filters: Filters = [[{ field: 'locationRef', operator: { _id: 'inq' }, value: locationIds }, { field: 'ownerRef', operator: { _id: 'eq' }, value: userId || this.session.userId }, { field: 'status', operator: { _id: 'eq' }, value: 'finished' }]];

    let options = [
      {
        $group: {
          _id: '$locationRef',
          finishedDate: { $max: '$finishedDate' },
          count: { $sum: 1 }
        }
      }
    ];
    return this.broker.aggregateQuery('missions', filters, options, null, null, false, null);
  }

  getStatsAndDistanceTransformAsync(position?: IPosition) {
    return (res: ResponseObject) => {
      return new Observable<ResponseObject>((observer: Observer<ResponseObject>) => {
        if (res && res.data && res.data.length > 0) {
          if (position) {
            res.data = this.calculateDistanceAndKpiData(res, position);
          }
          let ids = res.data.map((location: Location) => location._id);
          this.getLastVisitDate(ids).subscribe(dates => {
            res.data.forEach((location: Location) => {
              let date = dates.find(d => d._id === location._id);
              if (date && date.finishedDate) {
                location.lastVisit = date.finishedDate;
                location.countVisits = date.count;
              }
              observer.next({ count: res.count, data: <any>res.data });
              observer.complete();
            });
          });
        } else {
          observer.next({ count: 0, data: [] });
          observer.complete();
        }
      });
    };
  }

  getDistanceTransform() {
    return this.geoloc.getLocation().then((position: IPosition) => {
      return (res: ResponseObject) => {
        return new Observable<ResponseObject>((observer: Observer<ResponseObject>) => {
          if (res && res.data && res.data.length > 0) {
            res.data = this.calculateDistanceAndKpiData(res, position);
            observer.next({ count: res.count, data: <any>res.data });
            observer.complete();
          } else {
            observer.next({ count: 0, data: [] });
            observer.complete();
          }
        });
      };
    });
  }

  getDistanceAndLastVisitTransform(position: IPosition) {
    return (res: ResponseObject) => {
      return new Observable<ResponseObject>((observer: Observer<ResponseObject>) => {
        if (res && res.data && res.data.length > 0) {
          res.data = this.calculateDistanceAndKpiData(res, position);
          let ids = res.data.map((location: Location) => location._id);
          this.getLastVisitDate(ids).subscribe(dates => {
            res.data.forEach((location: Location) => {
              let date = dates.find(d => d._id === location._id);
              if (date && date.finishedDate) {
                location.lastVisit = date.finishedDate;
                location.countVisits = date.count;
              }
              observer.next({ count: res.count, data: <any>res.data });
              observer.complete();
            });
          });
        } else {
          observer.next({ count: 0, data: [] });
          observer.complete();
        }
      });
    };
  }

  calculateDistanceAndKpiData(res: ResponseObject, position: IPosition) {
    let retVal = res.data.map((location: Location) => {
      if (location._geoloc && position && isNumber(position.latitude) && isNumber(position.longitude)) {
        location.distance = this.geoloc.getDistance(position.latitude, position.longitude, location._geoloc[1], location._geoloc[0]);
      }

      let oldKpiData: Object = location.kpiData as any;
      let kpiData: Array<IKpiData> = [];
      if (this.session.tenant && this.session.tenant.locationKpis && this.session.tenant.locationKpis.length > 0) {
        this.session.tenant.locationKpis.forEach(kpi => {
          if (this.authentication.isAdmin() || (kpi.category === 'healthscore' && this.authentication.hasRole('healthscore')) || (kpi.category !== 'healthscore' && this.authentication.hasRole('storeinsights'))) {
            kpiData.push({
              category: kpi.category,
              value: oldKpiData && oldKpiData[kpi.category] ? oldKpiData[kpi.category].value : null,
              icon: kpi.icon,
              extraSymbol: kpi.extraSymbol,
              variation: oldKpiData && oldKpiData[kpi.category] ? oldKpiData[kpi.category].variation : null
            });
          }
        });
      }
      location.kpiData = kpiData;

      return location;
    });
    //retVal = orderBy(retVal, ['distance']);
    return retVal;
  }

  getAroundMeFilter(aroundMe: boolean, stats: boolean, forceRefresh: boolean): Promise<{ filters: Filters; sortModel: ISort[]; mapTransform: any; position: IPosition }> {
    let filters: Filters;
    let sortModel: Array<any>;
    let mapTransform: any;
    return this.geoloc.getLocation(forceRefresh).then((res: any) => {
      mapTransform = stats ? this.getStatsAndDistanceTransformAsync(res) : this.getDistanceAndLastVisitTransform(res);
      if (!aroundMe || !res) {
        filters = [[]];
        sortModel = [{ colId: 'title', sort: 'asc' }];
      } else {
        sortModel = [];
        filters = [
          [
            {
              field: '_geoloc',
              operator: { _id: 'nearSphere' },
              value: [res.longitude, res.latitude],
              max: 40000
            }
          ]
        ];
      }
      return { filters, sortModel, mapTransform, position: res };
    });
  }

  getLocationTypesTransform() {
    return (res: ResponseObject, search, filters, start, pageSize) => {
      return new Observable<ResponseObject>((observer: Observer<ResponseObject>) => {
        if (res && res.data && res.data.length > 0) {
          let ids = res.data.map((type: LocationType) => type._id);
          this.getLocationTypesStat(ids).subscribe(stats => {
            res.data.forEach((locationType: LocationType) => {
              let stat = stats.find(s => s._id === locationType._id);
              let count = stat ? stat.count || 0 : 0;
              locationType.count = count;
            });
            observer.next({ count: res.count, data: <any>res.data });
            observer.complete();
          });
        } else {
          observer.next({ count: 0, data: [] });
          observer.complete();
        }
      });
    };
  }

  getLocationTypesStat(locationTypeIds: Array<string>): Observable<Array<{ _id: string; count: number }>> {
    let filters: Filters = [[{ field: 'typeRef', operator: { _id: 'inq' }, value: locationTypeIds }]];
    let options = [
      {
        $project: {
          _id: '$typeRef'
        }
      },
      {
        $group: {
          _id: '$_id',
          count: { $sum: 1 }
        }
      }
    ];
    return this.broker.aggregateQuery('locations', filters, options);
  }

  getHealthscore(locationId: string, nbDays: Array<number> = [7, 30, 90]): Observable<Array<IHealthscore>> {
    if (this.network.isOffline()) {
      return from(this.cache.get(CACHE_KEYS.healthscore, locationId));
    } else {
      let url = this.config.apiUrl + 'locations/healthScore?storeId=' + locationId + '&days=[' + nbDays.toString() + ']';
      return this.rq.get(url).pipe(
        map(ret => {
          let retVal = ret.data ? ret.data : ret;
          this.cache.add(CACHE_KEYS.healthscore, locationId, retVal);
          return retVal;
        })
      );
    }
  }

  getUserLocations(userId: string) {
    let url = this.config.apiUrl + 'businesslogic/getUserLocations';
    return this.rq.post(url, { userId });
  }
}
