import { Injectable } from '@angular/core';
import { CoreConfig, Log } from '@shared/common';

import { File } from '../../interfaces/file/file.interface';
import { Requestor } from '../requestor/requestor.service';
import { Config } from '../config/config.service';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  IFilesService,
  isFile as _isFile,
  isBase64 as _isBase64,
  isFileUri as _isFileUri,
  isImageFile as _isImageFile,
  read as _read,
  getExtension as _getExtension,
  changeExtension as _changeExtension,
  getMaxSize as _getMaxSize,
  toPng as _toPng,
  isValid as _isValid,
  isImage as _isImage,
  isVideo as _isVideo,
  isAudio as _isAudio,
  isDocument as _isDocument,
  getType as _getType,
  getMimeType as _getMimeType,
  getIcon as _getIcon,
  getVideoPoster as _getVideoPoster,
  b64toBlob as _b64toBlob,
  blobToFile as _blobToFile,
  b64ToFile as _b64ToFile,
  saveBase64AsImageFile as _saveBase64AsImageFile,
  resizeBase64Image as _resizeBase64Image,
  getBase64MimeType as _getBase64MimeType,
  getBase64Extension as _getBase64Extension,
  resizeImage as _resizeImage,
  getNativeDirectory as _getNativeDirectory,
  resolveFilePath as _resolveFilePath,
  fixImageOrientation as _fixImageOrientation,
  moveToImageDirectory as _moveToImageDirectory,
  moveToImageDirectoryBase as _moveToImageDirectoryBase,
  fileNativeWriteFile as _fileNativeWriteFile,
  fileNativeCheckFile as _fileNativeCheckFile,
  fixAbsolutePath as _fixAbsolutePath,
  getCloudinaryUrl as _getCloudinaryUrl,
  getLocalFilePath as _getLocalFilePath,
  getLocalFileName as _getLocalFileName,
  saveToLocalFile as _saveToLocalFile,
  cleanFileName as _cleanFileName,
  getUrlWithAnnotations as _getUrlWithAnnotations,
  showPdfOnDevice as _showPdfOnDevice,
  downloadFileToDevice as _downloadFileToDevice,
  openBlob as _openBlob,
  afterOpenBlob as _afterOpenBlob
} from '@shared/stencil';

@Injectable()
export class Files implements IFilesService {
  constructor(private rq: Requestor, private coreConfig: CoreConfig, private log: Log, protected config: Config) {}

  isFile(file) {
    return _isFile(file);
  }

  isBase64(file) {
    return _isBase64(file);
  }

  isFileUri(file) {
    return _isFileUri(file);
  }

  isImageFile(file) {
    return _isImageFile(file);
  }

  read(nativeFile: File | Blob, type = 'blob', encoding?: string): Promise<string> {
    return _read(<any>nativeFile, type, encoding);
  }

  getExtension(file: File | any) {
    return _getExtension(file);
  }

  changeExtension(filename: string, extension: string): string {
    return _changeExtension(filename, extension);
  }

  getMaxSize(extension) {
    return _getMaxSize(extension);
  }

  toPng(value: string) {
    return _toPng(value);
  }

  isValid(file: File) {
    return _isValid(<any>file);
  }

  isImage(file: File | any) {
    return _isImage(file);
  }

  isVideo(file: File | any) {
    return _isVideo(file);
  }

  isAudio(file: File | any) {
    return _isAudio(file);
  }

  isDocument(file: File | any) {
    return _isDocument(file);
  }

  getType(file: File | any) {
    return _getType(file);
  }

  getMimeType(file: File | any) {
    return _getMimeType(file);
  }

  getIcon(file: File) {
    return _getIcon(file);
  }

  getVideoPoster(value: string) {
    return _getVideoPoster(value);
  }

  b64toBlob(b64Data, contentType = null, sliceSize = 512) {
    return _b64toBlob(b64Data, contentType, sliceSize);
  }

  blobToFile(blob): any {
    return _blobToFile(blob);
  }

  b64ToFile(data: string, file: File): any {
    return _b64ToFile(data, file as any);
  }

  saveBase64AsImageFile(data: string) {
    return _saveBase64AsImageFile(data);
  }

  resizeBase64Image(base64, maxWidth, maxHeight) {
    return _resizeBase64Image(base64, maxWidth, maxHeight);
  }

  getBase64MimeType(base64: string) {
    return _getBase64MimeType(base64);
  }

  getBase64Extension(base64: string) {
    return _getBase64Extension(base64);
  }

  resizeImage(file: File, maxWidth: Number, maxHeight: Number): Promise<any> {
    return _resizeImage(file as any, maxWidth, maxHeight);
  }

  getNativeDirectory(subfolder) {
    return _getNativeDirectory(subfolder);
  }

  resolveFilePath(filePath: string) {
    return _resolveFilePath(filePath);
  }

  fixImageOrientation(path, fileName) {
    return _fixImageOrientation(path, fileName);
  }

  //should only be called in cordova
  moveToImageDirectory(originalFilePath: string, disableOrientationFix: boolean = false): Promise<string> {
    return _moveToImageDirectory(originalFilePath, disableOrientationFix);
  }

  moveToImageDirectoryBase(fileDirPath: string, fileEntry: any, newName: string, originalFilePath: string) {
    return _moveToImageDirectoryBase(fileDirPath, fileEntry, newName, originalFilePath);
  }

  fileNativeWriteFile(path, fileName, text, options) {
    return _fileNativeWriteFile(path, fileName, text, options);
  }

  fileNativeCheckFile(path, fileName) {
    return _fileNativeCheckFile(path, fileName);
  }

  fixAbsolutePath(data: string) {
    return _fixAbsolutePath(data);
  }

  getCloudinaryUrl(retVal) {
    return _getCloudinaryUrl(retVal);
  }

  downloadFile(filename, mediaType, url, options) {
    return this.rq.downloadFile(filename, mediaType, url, options);
  }

  getLocalFilePath() {
    return _getLocalFilePath();
  }

  getLocalFileName(fileName: string) {
    return _getLocalFileName(fileName);
  }

  saveToLocalFile(blob, fileName) {
    return _saveToLocalFile(blob, fileName);
  }

  getUrlWithAnnotations(src: string, photo?: any) {
    return _getUrlWithAnnotations(src, photo);
  }

  cleanFileName(fileName: string) {
    return _cleanFileName(fileName);
  }

  downloadFileToDevice(source, target) {
    return _downloadFileToDevice(source, target);
  }

  showPdfOnDevice(filePath: string, fileName: string = null) {
    return _showPdfOnDevice(filePath, fileName);
  }

  uploadProxy(photoUrl: string): Observable<any> {
    let url = this.config.uploadProxyUrl;
    return this.rq.post(url, { url: photoUrl }).pipe(
      map(retVal => {
        return this.getCloudinaryUrl({ cloudinary: retVal });
      })
    );
  }

  exportToFile(content, type, encoding, filename) {
    encoding = encoding || 'charset=ISO-8859-1';
    filename = filename || 'data.csv';
    type = type || 'data:application/csv;charset=ISO-8859-1;';

    if (encoding === 'base64') {
      content = this.b64toBlob(content, type);
    }
    let blob = new Blob([content], {
      type: type
    });
    return this.rq.saveBlob(blob, filename);
  }

  openBlob(blob, fileName: string, mimeType: string) {
    return _openBlob(blob, fileName, mimeType);
  }

  afterOpenBlob(filePath, mimeType) {
    return _afterOpenBlob(filePath, mimeType);
  }

  _requestExternalStoragePermission() {
    return new Promise((resolve, reject) => {
      if (this.coreConfig.isAndroid() && this.coreConfig.isCordova()) {
        let permissions = (<any>window.cordova.plugins).permissions;
        permissions.hasPermission(permissions.READ_EXTERNAL_STORAGE, initialStatus => {
          if (!initialStatus.hasPermission) {
            let errorCallback = () => {
              this.log.error('Storage permission is not turned on');
              reject(false);
            };
            permissions.requestPermission(
              permissions.READ_EXTERNAL_STORAGE,
              status => {
                if (!status.hasPermission) {
                  errorCallback();
                } else {
                  resolve(true);
                }
              },
              errorCallback
            );
          } else {
            resolve(true);
          }
        });
      } else {
        resolve(true);
      }
    });
  }
}
