  getAllGoogleMaps(search?: string) {
    if (search) {
      return this.googlemaps.placePredictionsLocationObserver(search).pipe(
        map(predictions => {
          let details = flatten(predictions).filter(d => d.geometry && d.geometry.location);
          return {
            count: details.length,
            data: details.map(d => {
              let position = new Position(d.geometry.location);
              return {
                address: d.formatted_address,
                _id: d.id,
                _geoloc: position.toGeoLoc(true),
                coords: position.toJson()
              };
            })
          };
        })
      );
    } else {
      return from(this.googlemaps.getCurrentAddresses(true)).pipe(
        map(addresses => {
          return { count: addresses.length, data: addresses };
        })
      );
    }
  }

