  syncDatabase(lastSync?: Date, progressEvent?: EventEmitter<number>) {
    let collections = this.coreConfig.getSyncedCollections();
    if (collections && collections.length > 0) {
      this.log.log('Syncing Database - Start');
      let promises = collections.map((entry, i) => {
        return () => {
          if (entry.max <= 0) {
            return;
          }
          this.log.log('Syncing Database - Syncing ' + entry.name);
          return this.cache.getDatabaseCollection(entry.name).then(data => {
            let filters: Filters = cloneDeep(entry.filters || [[]]);
            filters.forEach(fs =>
              fs.forEach(f => {
                if (f.value && isArray(f.value) && f.value.indexOf('currentLanguage') >= 0) {
                  f.value[f.value.indexOf('currentLanguage')] = this.translate.getCurrentLanguage();
                }
              })
            );
            if (lastSync && data.length > 0) {
              // && ) {
              filters.forEach(f => f.push({ field: '_lmt', operator: { _id: 'gte' }, value: lastSync }));
            }

            return (
              this.getAll(entry.name, entry.fields, null, null, filters, null, 0, Math.min(entry.max || 1000, 1000), null, null, null, null, null, null, true)
                .toPromise()
                // let chunckNumber = (entry.max || 10000) / 100;
                // let ps = [];
                // for (let ii = 0; ii <= chunckNumber; ii++) {
                //   ps.push(this.getAll(entry.name, entry.fields, null, null, filters, null, ii * 100, 100).toPromise());
                //   //ps.push(this.aggregateQuery(entry.name, filters, [{ $skip: ii * 100 }, { $limit: 100 }], null, null, false, null, null).toPromise());
                // }
                // return Promise.all(ps)
                // .then((ret: Array<ResponseObject>) => {
                //   let d = [].concat.apply([], ret.map(r => r.data));
                //   return {
                //     count: d.length,
                //     data: d
                //   };
                // })
                .then(ret => {
                  if (progressEvent) {
                    progressEvent.emit((i / collections.length) * 100);
                  }
                  if (ret && ret.data) {
                    let filePromises: Array<any> = [Promise.resolve(null)];
                    let deletedKeys: Array<string>;
                    if (lastSync) {
                      filePromises = [
                        this.getDeletedEntities(entry.name, data)
                          .toPromise()
                          .then((deletedkeys: Array<string>) => {
                            deletedKeys = deletedkeys;
                          })
                      ];
                    }
                    if (entry.addToCache && entry.addToCache.length > 0) {
                      entry.addToCache.forEach(key => {
                        ret.data.forEach(item => {
                          if (item[key]) {
                            filePromises.push(() => cacheFile(item[key]));
                          }
                        });
                      });
                    }

                    return Promise.all(filePromises).then(() => {
                      return this.cache.updateDatabaseCollection(entry.name, ret.data, '_id', deletedKeys);
                    });
                  }
                })
            );
          });
        };
      });
      return this.promise.sequential(promises).then(ret => {
        if (progressEvent) {
          progressEvent.emit(100);
        }
        return ret;
      });
    }
    return Promise.resolve(null);
  }

  clearDatabase() {
    let collections = this.coreConfig.getSyncedCollections();
    if (collections && collections.length > 0) {
      let promises = collections.map(entry => {
        return () => this.cache.clearDatabaseCollection(entry.name);
      });
      return this.promise.sequential(promises);
    }
    return Promise.resolve(null);
  }

