import { Injectable, EventEmitter } from '@angular/core';
import { Broker } from '../broker/broker.service';
import { Requestor } from '../requestor/requestor.service';
import { Config } from '../config/config.service';

import { ITenant } from '@shared/stencil';
import { timer, of } from 'rxjs';
import { concatMap, catchError, filter, take } from 'rxjs/operators';
import { get } from 'lodash-es';

@Injectable()
export class Tenants {
  constructor(protected rq: Requestor, protected broker: Broker, protected config: Config) {}

  clone(from: ITenant, to: ITenant, progress?: EventEmitter<number>) {
    return this.rq
      .post(this.broker.getApiUrl() + 'demoManagement/CopyTenantToTenant', {
        copyFromId: from._id,
        copyToId: to._id
      })
      .toPromise()
      .then(ret => {
        if (ret._id) {
          return timer(1000, 500)
            .pipe(
              concatMap(() => {
                return this.rq.get(this.config.publicApiUrl + 'jobs/' + ret._id, false, null).pipe(
                  catchError(() => {
                    return of({});
                  })
                );
              })
            )
            .pipe(
              filter(s => {
                if (progress && s && s.progress) {
                  progress.emit(s.progress * 100);
                }
                return s && s.progress === 1;
              })
            )
            .pipe(take(1))
            .toPromise()
            .then(res => {
              let errors = get(res, 'data.output.errors');
              let data = get(res, 'data.output.data');
              return { data, errors };
            });
        }
      });
  }
}
