import { IProperty } from '../entity/entity.interface';
import { FormFieldType, ILocation, IKpiData } from '@shared/stencil';
import { LocationType } from '../location-type/location-type.interface';
import { Model } from '../../decorators/model/model.decorator';
import { Searchable } from '../../decorators/searchable/searchable.decorator';
import { Editable } from '../../decorators/editable/editable.decorator';
import { Mapping } from '../../decorators/mapping/mapping.decorator';
import { ROLES_CONDITIONS } from '../condition/icondition.interface';
import { Tenant } from '../tenant/tenant.interface';

export const LOCATION_GEOCODESTATUS = ['file', 'geocoder', 'place', 'prediction', 'error'];

export function onAddressChange(value, data, field) {
  if (value && value._geoloc) {
    data._geoloc = value._geoloc;
    (<any>field)._geoloc = value._geoloc;
    data.address = value.address;
  }
}

export function onTypeChange(value, data) {
  if (value && value._id) {
    data.typeRef = value._id;
  }
}

@Model({
  className: 'Location',
  collectionName: 'locations',
  fields: ['*', 'type.name'],
  include: ['type', '_tenant'],
  icon: 'yo-store'
})
export class Location implements ILocation {
  @Mapping('Location', { order: 12 })
  @Editable('Location', {
    type: FormFieldType.text,
    visible: false,
    forceExport: true,
    exportOrder: 13,
    filterable: false
  })
  _id: string;

  @Mapping('Location', { order: 11 })
  @Editable('Location', {
    type: FormFieldType.text,
    columnDefinition: { width: 100 },
    exportOrder: 12
  })
  @Searchable('Location')
  clientid: string;

  @Mapping('Location', { order: 0 })
  @Editable('Location', {
    required: true,
    type: FormFieldType.text,
    exportOrder: 1,
    sortable: true
  })
  @Searchable('Location')
  title: string;

  @Mapping('Location', { order: 1 })
  @Editable('Location', {
    required: true,
    type: FormFieldType.address,
    filterName: '_geoloc',
    columnDefinition: { width: 350 },
    showMap: true,
    exportOrder: 2,
    onChange: onAddressChange
  })
  @Searchable('Location')
  address: string;

  @Mapping('Location', { order: 15 })
  @Editable('Location', {
    type: FormFieldType.photo,
    filterable: false,
    title: 'PHOTO',
    columnDefinition: { width: 52 },
    exportOrder: 16,
    sortable: false,
    allowLibrary: true
  })
  imageData?: string;

  @Mapping('Location', { order: 13 })
  @Editable('Location', {
    type: FormFieldType.checkbox,
    columnDefinition: { width: 40 },
    exportOrder: 14,
    filterable: true
  })
  vip: boolean;

  @Mapping('Location', { order: 9 })
  @Editable('Location', {
    title: 'LOCATIONTAGS',
    type: FormFieldType.autocomplete,
    tag: true,
    collectionName: 'locations',
    multiple: true,
    subQuery: { field: 'locationRef', values: '_id' },
    icon: 'yo-tag',
    exportOrder: 10
  })
  tags: Array<string>;

  @Mapping('Location', { order: 2 })
  @Editable('Location', {
    type: FormFieldType.autocomplete,
    collectionName: 'locationtypes',
    required: true,
    columnDefinition: { name: 'name' },
    exportOrder: 3,
    filterableAdvanced: true,
    onChange: onTypeChange,
    filterable: true
  })
  type: LocationType;
  typeRef: string;

  @Mapping('Location', { order: 14 })
  @Editable('Location', {
    type: FormFieldType.autocomplete,
    collectionName: 'missiondescriptions',
    clearable: true,
    multiple: true,
    filterable: false,
    sortable: false,
    suppressExport: true,
    filters: [[{ field: 'archived', operator: { _id: 'neq' }, value: true }]],
    hiddenFields: ['archived']
  })
  missiondescriptions?;
  missiondescriptionsRef?: Array<string>;

  @Mapping('Location', { order: 10 })
  @Editable('Location', {
    title: 'NOTIFICATIONEMAILS',
    type: FormFieldType.emailreport,
    showUsers: true,
    stateful: false,
    tab: 'INFORMATION',
    exportOrder: 11,
    filterableAdvanced: true
  })
  notificationemail?: Array<string>;

  @Mapping('Location', { order: 5 })
  @Editable('Location', {
    type: FormFieldType.text,
    tab: 'INFORMATION',
    exportOrder: 6,
    filterableAdvanced: true
  })
  @Searchable('Location')
  contactname?: string;

  @Mapping('Location', { order: 6 })
  @Editable('Location', {
    type: FormFieldType.email,
    tab: 'INFORMATION',
    exportOrder: 7,
    filterableAdvanced: true
  })
  contactemail?: string;

  @Mapping('Location', { order: 7 })
  @Editable('Location', {
    type: FormFieldType.text,
    tab: 'INFORMATION',
    exportOrder: 8,
    filterableAdvanced: true
  })
  contactphone?: string;

  @Mapping('Location', { order: 8 })
  @Editable('Location', {
    type: FormFieldType.textarea,
    tab: 'INFORMATION',
    exportOrder: 9,
    filterableAdvanced: true
  })
  info?: string;

  @Editable('Location', {
    readonly: true,
    type: FormFieldType.autocomplete,
    values: LOCATION_GEOCODESTATUS,
    clearable: false,
    filterableAdvanced: true,
    suppressExport: true
  })
  status?: string;

  @Mapping('Location', { order: 4 })
  @Editable('Location', {
    visible: false,
    exportOrder: 5,
    forceExport: true,
    filterable: false,
    sortable: false
  })
  placesearch?: string;

  @Mapping('Location', { order: 3 })
  @Editable('Location', {
    visible: false,
    forceExport: true,
    exportOrder: 4,
    filterable: false,
    sortable: false
  })
  _geoloc: [number, number];

  @Editable('Location', {
    type: FormFieldType.autocomplete,
    tab: 'PROPERTIES',
    allowCustomTag: true,
    multiple: true,
    filterable: false,
    sortable: false,
    suppressExport: true,
    condition: ROLES_CONDITIONS.hasProductBatch
  })
  aisles?: string[];

  @Editable('Location', {
    type: FormFieldType.text,
    tab: 'PROPERTIES',
    condition: ROLES_CONDITIONS.hasProductBatch
  })
  smartTagId?: string;

  @Editable('Location', {
    type: FormFieldType.json,
    tab: 'PROPERTIES',
    filterable: false,
    sortable: false,
    suppressExport: true,
    condition: ROLES_CONDITIONS.isAdmin
  })
  properties?: IProperty[];

  distance?: number;
  stats?: Array<{ title: string; value: number; color: string }>;

  @Editable('Location', {
    required: true,
    title: 'TENANT',
    type: FormFieldType.autocomplete,
    condition: [ROLES_CONDITIONS.isAdmin],
    collectionName: 'tenants',
    multiple: false,
    columnDefinition: { name: 'name' }
  })
  _tenant?: Tenant;
  _tenantRef?: string;

  lastVisit?: Date;
  countVisits?: number;

  @Editable('Location', {
    visible: false,
    type: FormFieldType.toggle,
    filterable: false,
    invertedSort: true
  })
  hasAvailableMissions?: boolean;

  @Editable('Location', {
    visible: false,
    type: FormFieldType.number,
    filterable: false,
    invertedSort: true
  })
  availableMissions?: number;

  kpiData?: Array<IKpiData>;
}
