import { Injectable, EventEmitter } from '@angular/core';
import { Requestor } from '../requestor/requestor.service';
import { Files } from '../files/files.service';
import { Config } from '../config/config.service';
import { Models } from '../models/models.service';
import { Authentication } from '../authentication/authentication.service';

import { IFormField, Query, SubQuery, FormFieldType, downloadFile, IColumnDefinition } from '@shared/stencil';

import { compact, sortBy, cloneDeep, uniq, get } from 'lodash-es';
import { timer, of } from 'rxjs';
import { concatMap, filter, take, catchError } from 'rxjs/operators';
@Injectable()
export class Print {
  protected excludedDashboardColumns: Array<string> = ['validated', 'validatedBy', 'owner.username', 'creator.username', 'user.username'];

  constructor(protected rq: Requestor, protected config: Config, protected authentication: Authentication, protected files: Files) {}

  printToMapping(collectionName: string, columns: Array<IFormField>, query: Query, subQuery: SubQuery, aggregateOptions: Array<any>, type: 'csv' | 'xlsx', campaignFields: Array<IFormField>, filename?: string, progress?: EventEmitter<number>) {
    let publicCollectionName = Models.getPublicCollectionName(collectionName);
    let url = this.config.publicApiUrl + publicCollectionName + '/export';
    type = type === 'xlsx' ? 'xlsx' : 'csv';
    //query.limit = 25500;
    if (subQuery) {
      query.subQuery = subQuery;
    }

    let model = Models.getModelByCollectionName(collectionName);
    query.include = model.include;

    let exportColumns = sortBy(cloneDeep(columns.filter(c => !c.suppressExport)), c => c.exportOrder || 100).map((c: any) => {
      if (c.field) {
        c.field = c.field.replace(/\?/g, '');
      }
      return c;
    });

    if (this.authentication.isDashboard()) {
      exportColumns = exportColumns.filter(c => this.excludedDashboardColumns.indexOf(c.field) < 0);
    }
    //let mime = type === 'csv' ? 'text/plain' : 'application/xlsx';
    filename = this.files.cleanFileName(filename || publicCollectionName) + this.rq.getFilenameSuffix() + '.' + type;
    let cacheQuery: any = {};
    if (campaignFields && campaignFields.length > 0) {
      let catalogs = uniq(campaignFields.filter(f => f.type === FormFieldType.catalog).map(f => f.catalog));
      if (catalogs.length > 0) {
        cacheQuery.products = { where: { catalogRef: { inq: catalogs } } };
      }
      let collectionNames = compact(uniq(campaignFields.filter(f => f.type === FormFieldType.autocomplete).map(f => f.collectionName)));
      if (collectionNames.length > 0) {
        cacheQuery.custommodels = { where: { name: { inq: collectionNames } }, cacheKey: 'name' };
      }
    }
    query = cloneDeep(query);
    delete query.fields;
    return this.rq
      .get(url, false, null, [{ name: 'filter', value: JSON.stringify(query) }, { name: 'type', value: type === 'xlsx' ? 'excel' : 'csv' }]) //r.token
      .toPromise()
      .then(ret => {
        if (ret._id) {
          return timer(1000, 500)
            .pipe(
              concatMap(() => {
                return this.rq.get(this.config.publicApiUrl + 'jobs/' + ret._id, false, null).pipe(
                  catchError(() => {
                    return of({});
                  })
                );
              })
            )
            .pipe(
              filter(s => {
                if (progress && s && s.progress) {
                  progress.emit(s.progress * 100);
                }
                return s && s.progress === 1;
              })
            )
            .pipe(take(1))
            .toPromise()
            .then(res => {
              let fileUrl = get(res, 'data.output.data.download_url');
              if (fileUrl) {
                return downloadFile(fileUrl, filename);
              }
            });
        }
      });
  }

  printToSpreadsheet(collectionName: string, columns: Array<IColumnDefinition>, query: Query, subQuery: SubQuery, aggregateOptions: Array<any>, channel: string, type: string, campaignFields: Array<IFormField>, filename?: string) {
    let url = this.config.apiUrl + 'print/write-spreadsheet';
    let fixedCollectionName = Models.fixCollectionName(collectionName);
    type = type === 'csv' ? 'csv' : 'xlsx';
    //query.limit = 25500;
    if (subQuery) {
      query.subQuery = subQuery;
    }

    let model = Models.getModelByCollectionName(collectionName);
    query.include = model.include;

    let exportColumns = sortBy(cloneDeep(columns.filter(c => !c.suppressExport)), (c: any) => c.exportOrder || 100).map((c: any) => {
      if (c.field) {
        c.field = c.field.replace(/\?/g, '');
      }
      return c;
    });

    if (this.authentication.isDashboard()) {
      exportColumns = exportColumns.filter(c => this.excludedDashboardColumns.indexOf(c.field) < 0);
    }
    let mime = type === 'csv' ? 'text/plain' : 'application/xlsx';
    filename = this.files.cleanFileName(filename || fixedCollectionName) + this.rq.getFilenameSuffix() + '.' + type;
    let cacheQuery: any = {};
    if (campaignFields && campaignFields.length > 0) {
      let catalogs = uniq(campaignFields.filter(f => f.type === FormFieldType.catalog).map(f => f.catalog));
      if (catalogs.length > 0) {
        cacheQuery.products = { where: { catalogRef: { inq: catalogs } } };
      }
      let collectionNames = compact(uniq(campaignFields.filter(f => f.type === FormFieldType.autocomplete).map(f => f.collectionName)));
      if (collectionNames.length > 0) {
        cacheQuery.custommodels = { where: { name: { inq: collectionNames } }, cacheKey: 'name' };
      }
    }

    let body = {
      collectionName: fixedCollectionName,
      columns: exportColumns,
      query,
      aggregateOptions,
      channel,
      type,
      cacheQuery
    };
    return this.rq.downloadFile(filename, mime, url, {
      method: 'post',
      headers: { 'Content-Type': 'application/json' },
      body: body
    });
  }
}
