import { Editable } from '../../decorators/editable/editable.decorator';
import { Searchable } from '../../decorators/searchable/searchable.decorator';
import { FormFieldType, INotification } from '@shared/stencil';
import { Model } from '../../decorators/model/model.decorator';
import { User } from '../user/user.interface';

@Model({
  className: 'Notification',
  collectionName: 'notifications',
  fields: ['*', 'sender._id', 'sender.firstName', 'sender.lastName', 'sender.username', 'sender.imageData', 'sender.email', 'sender.telephone'],
  include: ['sender'], //'creator'
  icon: 'yo-activity'
})
export class Notification extends INotification {
  @Editable('Notification', {
    type: FormFieldType.autocomplete,
    translate: true,
    values: ['email', 'notification', 'allnotification'],
    required: true,
    filterable: true
  })
  mode: 'email' | 'notification' | 'allnotification';

  @Editable('Notification', { type: FormFieldType.text, required: true })
  @Searchable('Notification')
  title: string;

  @Editable('Notification', { type: FormFieldType.textarea, required: true })
  body?: string;

  @Editable('Notification', {
    type: FormFieldType.datetime,
    minDate: new Date(),
    condition: 'mode!="sms"'
  })
  scheduledDate?: Date;

  //@Editable('Notification', { type: FormFieldType.autocomplete, values: ['com.ipelia.yoobicv3', 'com.kering.yoobic', 'com.ipelia.yooask'], condition: ROLES_CONDITIONS.isAdmin, clearable: true, translate: true, multiple: true, advanced: true })
  apps?: Array<string>;

  @Editable('Notification', {
    type: FormFieldType.autocomplete,
    collectionName: 'user',
    icon: 'yo-user',
    columnDefinition: { name: 'username' },
    exportOrder: 24,
    title: 'SENDBY',
    filterable: true
  })
  sender?: User;
  senderRef?: string;
}
