import { Model } from '../../decorators/model/model.decorator';
import { Editable } from '../../decorators/editable/editable.decorator';
import { IFormField, FormFieldType, ITodo, ITodoTask, ITodoTaskSimple } from '@shared/stencil';
import { User } from '../user/user.interface';

export function onUserMeActionHandler() {
  this.selectMatch(this.session.user);
}

@Model({ className: 'Todo' })
export class Todo extends ITodo {
  @Editable('Todo', { type: FormFieldType.text, name: 'title', required: true })
  title: string;

  @Editable('Todo', {
    title: 'ASSIGNTO',
    name: 'user',
    type: FormFieldType.autocomplete,
    collectionName: 'user',
    queryFields: User.getSimpleFields(),
    multiple: false,
    required: true,
    extraButton: { title: 'ASSIGNTO', buttons: [{ text: 'ME', handler: onUserMeActionHandler }] }
  })
  user: any;

  @Editable('Todo', {
    title: 'DUEDATE',
    type: FormFieldType.datetime,
    required: false,
    clearable: true,
    minDate: new Date(),
    secondary: true,
    icon: 'yo-calendar'
  })
  duedate: Date;

  @Editable('Todo', {
    title: 'NOTIFICATIONEMAILS',
    type: FormFieldType.emailreport,
    showUsers: true,
    secondary: true,
    icon: 'yo-email'
  })
  notificationemail?: Array<string>;

  @Editable('Todo', {
    title: 'REMINDER',
    type: FormFieldType.datetime,
    required: false,
    clearable: true,
    secondary: true,
    icon: 'yo-activity'
  })
  reminderdate?: Date;

  @Editable('Todo', { type: FormFieldType.starrating, secondary: true, icon: 'yo-priority' })
  priority?: number;

  field?: IFormField;
}

@Model({ className: 'TodoTask' })
export class TodoTask extends ITodoTask {
  @Editable('TodoTask', { type: FormFieldType.text, title: 'TITLE', required: true })
  text: { value: string };

  @Editable('TodoTask', { type: FormFieldType.textarea, required: false })
  comments?: { value: string };

  @Editable('TodoTask', {
    type: FormFieldType.datetime,
    required: false,
    clearable: true,
    minDate: new Date()
  })
  duedate?: { value: Date };

  @Editable('TodoTask', { type: FormFieldType.toggle, required: false, title: 'PHOTO', flex: 50 })
  hasphoto?: { value: boolean };

  @Editable('TodoTask', {
    type: FormFieldType.toggle,
    required: false,
    title: 'MANDATORYPHOTO',
    flex: 50,
    condition: 'hasphoto.value==1'
  })
  isphotorequired?: { value: boolean };

  @Editable('TodoTask', {
    type: FormFieldType.toggle,
    required: false,
    title: 'ALLOWLIBRARY',
    flex: 50,
    condition: 'hasphoto.value==1'
  })
  allowLibrary?: { value: boolean };

  field?: IFormField;
}

@Model({ className: 'TodoTaskSimple' })
export class TodoTaskSimple extends ITodoTaskSimple {
  @Editable('TodoTaskSimple', {
    type: FormFieldType.textarea,
    title: 'DESCRIPTION',
    required: true
  })
  text: { value: string };

  @Editable('TodoTaskSimple', {
    title: 'ASSIGNTO',
    name: 'user',
    type: FormFieldType.autocomplete,
    queryFields: User.getSimpleFields(),
    collectionName: 'user',
    multiple: false,
    required: true,
    extraButton: { title: 'ASSIGNTO', buttons: [{ text: 'ME', handler: onUserMeActionHandler }] }
  })
  user: any;

  @Editable('TodoTaskSimple', {
    type: FormFieldType.datetime,
    required: false,
    clearable: true,
    minDate: new Date()
  })
  duedate?: { value: Date };

  @Editable('TodoTaskSimple', {
    type: FormFieldType.toggle,
    required: false,
    title: 'ASKFORPHOTO',
    flex: 50
  })
  hasphoto?: { value: boolean };

  @Editable('TodoTaskSimple', {
    type: FormFieldType.toggle,
    required: false,
    title: 'MANDATORYPHOTO',
    flex: 50,
    condition: 'hasphoto.value==1'
  })
  isphotorequired?: { value: boolean };

  @Editable('TodoTaskSimple', {
    type: FormFieldType.toggle,
    required: false,
    title: 'ALLOWLIBRARY',
    flex: 50,
    condition: 'hasphoto.value==1'
  })
  allowLibrary?: { value: boolean };
  field?: IFormField;
}
