import { ChartDefinition } from '../chart-definition/chart-definition.interface';

import { Model } from '../../decorators/model/model.decorator';
import { Searchable } from '../../decorators/searchable/searchable.decorator';
import { Editable } from '../../decorators/editable/editable.decorator';
import { FormFieldType, IDashboard } from '@shared/stencil';
import { Tenant } from '../tenant/tenant.interface';
import { ROLES_CONDITIONS } from '../condition/condition.interface';

@Model({
  className: 'Dashboard',
  collectionName: 'dashboards',
  fields: ['*'],
  include: []
})
export class Dashboard extends IDashboard {
  @Editable('Dashboard', { required: true, type: FormFieldType.text, sortable: true })
  @Searchable('Dashboard')
  title: string;

  @Editable('Dashboard', { required: false, type: FormFieldType.textarea })
  @Searchable('Dashboard')
  description: string;

  @Editable('Dashboard', {
    name: '_lmt',
    title: 'DATE',
    type: FormFieldType.datetime,
    readonly: true,
    sortable: true,
    filterableAdvanced: true,
    advanced: true
  })
  _lmt: any;

  tabs: Array<{
    title: string;
    items: Array<{
      col: number;
      row: number;
      sizex: number;
      sizey: number;
      definition: ChartDefinition;
    }>;
  }>;

  @Editable('Dashboard', {
    required: true,
    title: 'TENANT',
    type: FormFieldType.autocomplete,
    collectionName: 'tenants',
    columnDefinition: { name: 'name' },
    multiple: false,
    filterable: true,
    condition: [ROLES_CONDITIONS.isAdmin]
  })
  _tenant: Tenant;
  _tenantRef?: string;
}
