import { Injectable } from '@angular/core';
//import { User } from '../../interfaces/user/user.interface';
import { Currency } from '../../interfaces/currency/currency.interface';
import { Location } from '../../interfaces/location/location.interface';
import { Tenant } from '../../interfaces/tenant/tenant.interface';
import { Config } from '../config/config.service';

import { MissionDescription } from '../../interfaces/mission-description/mission-description.interface';
import { Persistent, LocalForageService } from '@shared/common';
import { IUser, ISessionService, ITranslation } from '@shared/stencil';

@Injectable()
export class Session implements ISessionService {
  public token: string;
  public user: IUser;
  public userId: string;
  public currencies: Array<Currency>;
  public groups: Array<string>;
  public roles: Array<string>;
  public tenant: Tenant;
  public photoAnnotationTranslations: Array<ITranslation>;
  public cameraStarted: boolean;
  public hasScandit: boolean;

  @Persistent('login.hideWalkthrough') public hideWalkthrough: boolean;
  @Persistent('badges') localPendingBadges;
  @Persistent('debugEvent') debugEvent: boolean;

  public openedChannels: Array<string> = [];

  public selectedMissionDescription: MissionDescription = null;
  public selectedLocation: Location = null;

  constructor(protected localForage: LocalForageService, protected config: Config) {}

  get apiUrl() {
    return this.config.apiUrl;
  }

  clear(clearUser = true) {
    if (clearUser) {
      this.token = '';
      this.user = null;
      this.userId = null;
      this.tenant = null;
      this.currencies = [];
      this.groups = [];
      this.roles = [];
      this.hideWalkthrough = false;
      this.localPendingBadges = {};
      this.debugEvent = false;
    }
    return Promise.resolve();
  }
}
