import { Injectable } from '@angular/core';

import { IEntity, getUUID } from '@shared/stencil';
import { LocalForageService, PromiseService, Log } from '@shared/common';

import { CACHE_KEYS } from '../../interfaces/cache/cache.interface';

import { from } from 'rxjs';
import { keys } from 'lodash-es';

@Injectable()
export class Cache {
  public static OFFLINE_PREFIX = 'offline_';

  constructor(protected localForage: LocalForageService, protected promise: PromiseService, protected log: Log) {}

  add(cacheKey: string, entryKey: string, item: any) {
    return this.localForage
      .get(cacheKey)
      .then((data: any) => {
        data = data || {};
        if (entryKey) {
          data[entryKey] = item;
        }
        return this.localForage.set(cacheKey, data);
      })
      .then(() => item);
  }

  get(cacheKey: string, entryKey: string) {
    return from(
      this.localForage.get(cacheKey).then(data => {
        data = data || {};
        return data[entryKey] || {};
      })
    );
  }

  getAll(cacheKey: string) {
    return this.localForage.get(cacheKey).then((data: any) => {
      data = data || {};
      let retVal = [];
      keys(data).forEach(key => {
        if (data[key]) {
          retVal.push(data[key]);
        }
      });
      return retVal;
    });
  }

  remove(cacheKey: string, entryKey: string) {
    return from(
      this.localForage.get(cacheKey).then(data => {
        data = data || {};
        delete data[entryKey];
        return this.localForage.set(cacheKey, data);
      })
    );
  }

  getDatabaseCacheKey(collectionName: string) {
    return CACHE_KEYS.database + '.' + collectionName;
  }

  getDatabaseCollection<T = IEntity>(collectionName: string): Promise<Array<T>> {
    let cacheKey = this.getDatabaseCacheKey(collectionName);
    return this.localForage.get(cacheKey).then((data: Array<T>) => {
      return data || [];
    });
  }

  setDatabaseCollection<T = IEntity>(collectionName: string, items: Array<T>): Promise<Array<T>> {
    let cacheKey = this.getDatabaseCacheKey(collectionName);
    return this.localForage.set(cacheKey, items);
  }

  updateDatabaseCollection<T = IEntity>(collectionName: string, items: Array<T>, idField: string = '_id', deletedKeys?: Array<string>) {
    let cacheKey = this.getDatabaseCacheKey(collectionName);
    return this.localForage.get(cacheKey).then((data: Array<T>) => {
      data = data || [];
      if (items && items.length > 0) {
        data = data.filter(d => items.findIndex(r => r[idField] === d[idField]) < 0);
      }
      data = [...data, ...items];
      if (deletedKeys && deletedKeys.length > 0) {
        data = data.filter(d => deletedKeys.findIndex(r => r[idField] === d[idField]) < 0);
      }
      this.log.log('Syncing Database - Syncing ' + collectionName + ' done : ' + data.length + ' entities locally');
      return this.localForage.set(cacheKey, data);
    });
  }

  clearDatabaseCollection(collectionName: string) {
    let cacheKey = this.getDatabaseCacheKey(collectionName);
    this.log.log('Syncing Database - Clearing ' + collectionName);
    return this.localForage.remove(cacheKey);
  }

  upsertInDatabaseCollection<T = IEntity>(collectionName: string, entity: T, patch: boolean = false, idField: string = '_id') {
    entity[idField] = entity[idField] || Cache.OFFLINE_PREFIX + getUUID();
    return this.getByIdFromDatabaseCollection(collectionName, entity[idField]).then(retVal => {
      if (retVal && patch) {
        entity = { ...retVal, ...(entity as any) };
      }
      return this.updateDatabaseCollection(collectionName, [entity], idField).then(() => {
        return entity;
      });
    });
  }

  removeFromDatabaseCollection<T = IEntity>(collectionName: string, id: string, idField: string = '_id') {
    return this.getDatabaseCollection<T>(collectionName).then(data => {
      data = data.filter(d => d[idField] !== id);
      return this.setDatabaseCollection<T>(collectionName, data);
    });
  }

  replaceFromDatabaseCollection<T = IEntity>(collectionName: string, id: string, newEntity: T, idField: string = '_id') {
    return this.getDatabaseCollection<T>(collectionName).then(data => {
      data = data.filter(d => d[idField] !== id);
      data = [...data, newEntity];
      return this.setDatabaseCollection<T>(collectionName, data);
    });
  }

  getByIdFromDatabaseCollection<T = IEntity>(collectionName: string, id: string, idField: string = '_id') {
    return this.getDatabaseCollection<T>(collectionName).then(data => {
      let retVal = data.find(d => d[idField] === id);
      return retVal;
    });
  }
}
