import { Injectable } from '@angular/core';
import { CoreConfig } from '@shared/common';

import { Requestor } from '../requestor/requestor.service';
import { map } from 'rxjs/operators';

@Injectable()
export class Unsplash {
  constructor(protected rq: Requestor, protected coreConfig: CoreConfig) {}

  getAll(search?: string, skip?: number, limit?: number) {
    let url;
    if (search) {
      url = 'https://api.unsplash.com/search/photos?client_id=' + this.coreConfig.getKey('unsplashAccessKey');
      url += '&query=' + search;
      url += '&page=' + (skip / limit + 1);
      url += '&per_page=' + limit;
    } else {
      url = 'https://api.unsplash.com/photos?page=1&per_page=20&client_id=' + this.coreConfig.getKey('unsplashAccessKey');
    }
    return this.rq.get(url, null, null, null, true).pipe(
      map(ret => {
        //{
        //count: search ? ret.total : 100,
        //data:
        return (search ? ret.results : ret).map(r => ({
          _id: r.id,
          title: r.description,
          value: r.urls.raw,
          thumb: r.urls.thumb
        }));
        //};
      })
    );
  }
}
